<template>
  <div class="dialog dialog-23 dialog-dom">
    <div ref="dialog4" class="dialogs" @click.native="moveNext">
      <template v-for="(item, index) in dialogs.dialog23">
        <div v-if="dialogCurrent === index && item.type === 'dialog'" :key="index" class="animated fadeIn" :class="item.class + ' dialog' + (index + 1)">
          <span>{{item.content}}</span>
        </div>
        <div
          v-else-if="dialogCurrent === index && item.type === 'action'"
          :key="index"
          :class="item.class + ' dialog' + (index + 1)"
        >
          <div class="animated fadeUp">
            <!-- <div class=""> -->
            <img src="../assets/shanziwenzi.png" alt>
          </div>
        </div>
      </template>
    </div>
  </div>
</template>

<script>
import dialogs from '../dialogs'

export default {
  name: 'HelloWorld',
  props: ['dialogCurrent'],
  data () {
    return {
      dialogs,
    }
  },
  watch: {
    dialogCurrent (val) {
      console.log('dialogCurrentWatch', val, dialogs.dialog23 )
      if (val > dialogs.dialog23.length - 1) {
        this.$emit('move-next')
      }
    }
  },
  methods: {
    moveNext(e) {
      e.preventDefault()
      console.log('moveNext')
      this.$emit('move-next')
    }
  }
}
</script>

